import store from '../index'
import router from '../../router/index'
import $ from 'jquery'
export default {
    state: {
        info: '' // token
    },
    mutations: {
        getInfo(state, token) {
            $.ajax({
                url: process.env.API_HOST + '/basic/getRoleByUser',
                type: 'POST', //GET
                async: false, //false,
                data: {
                    userid: token
                },
                timeout: 5000, //
                dataType: 'json', //
                beforeSend: function (xhr) { },
                success: function (data, textStatus, jqXHR) {
                    console.log(data);
                    state.info = data
                    if (state.info.role === "false") {
                        state.info.role = ""
                    }
                },
                error: function (xhr, textStatus) { },
                complete: function () { }
            })
            // state.info = {
            //     role: 'a'
            // }
            sessionStorage.setItem('info', JSON.stringify(store.getters.info))
        },
        setRole(state, options) { // 
            state.info = {
                role: options.role,
                permissions: options.permissions
            }
            sessionStorage.setItem('info', JSON.stringify(store.getters.info));
            store.dispatch('newRoutes', options.role)
            router.addRoutes(store.getters.addRouters)

        }
    },
    actions: {
        getInfo({ commit }, token) {
            commit('getInfo', token)
        },
        setRole({ commit }, options) {
            // 
            commit('setRole', options)
        }
    }
}


