<template>
  <div class="xiaxing">
      <el-select style="width:90px;margin-right:20px;" v-model="destTypevalue" placeholder="">
        <el-option
          v-for="item in destType"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        ></el-option>
      </el-select>

      <el-select style="width:90px" v-model="value" placeholder="">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        ></el-option>
      </el-select>
      <el-select style="margin-left:20px;" v-model="mainBasestationCodevalue" placeholder="">
      <el-option
        v-for="item in mainBasestationCode"
        :key="item.maincode"
        :label="item.mainname"
        :value="item.maincode"
      ></el-option>
    </el-select>
    <el-select style="margin-left: 221px;margin-top:20px;" v-model="basestationCodesvalue" multiple collapse-tags placeholder="">
      <el-option v-for="item in basestationCodes" :key="item.code" :label="item.name" :value="item.code"></el-option>
    </el-select>
      <!-- <el-button type="primary" style="margin-left:20px;" @click="controlLamp"></el-button> -->
      <!-- <el-input v-model="sensitivity" placeholder="" style="width:100px;"></el-input> -->
      <el-button
        type="primary"
        style="margin-left:20px;margin-right:20px;"
        round
        @click="controlLamp"
      ></el-button>
    </div>

</template>

<script>
export default {
  data() {
    return {
        basestationCodes: [],
        basestationCodesvalue: '',
        mainBasestationCode: [],
      mainBasestationCodevalue: "",
        value: "",
      destTypevalue: "",
      destType: [
        {
          value: "01",
          label: ""
        },
        {
          value: "02",
          label: "BBB"
        },
        {
          value: "03",
          label: "868/434"
        },
        {
          value: "04",
          label: "868/434"
        },
        {
          value: "05",
          label: ""
        },
        {
          value: "06",
          label: ""
        }
      ],
      options: [
        {
          value: "01",
          label: ""
        },
        {
          value: "02",
          label: ""
        },
        {
          value: "03",
          label: ""
        },
        {
          value: "04",
          label: ""
        },
        {
          value: "05",
          label: ""
        },
        {
          value: "06",
          label: ""
        }
      ]
    };
  },
  created() {
    // 
    this.$ajax
      .post(
        process.env.API_HOST +
          "/basBasestationCheck/getBasMainbasestationPOList"
      )
      .then(respones => {
        console.log(respones.data, "");
        this.mainBasestationCode = respones.data;
      });
  },
    watch:{
      mainBasestationCodevalue(val){
          this.basestationCodesvalue=[]

// 
 this.$ajax
      .post(
        process.env.API_HOST +
          "mainBasestation/getChildBasestationByMain",`mainBasestationCode=${val}`
      )
      .then(respones => {
        console.log(respones.data, "");
        this.basestationCodes = respones.data;
      });
      },

  },
  methods:{
      controlLamp() {
      // 
      this.$ajax
        .post(
          process.env.API_HOST + "/baseStationTransmission/controlLamp",
          `type=${this.value}&mainBasestationCode=${this.mainBasestationCodevalue}&basestationCodes=${this.basestationCodesvalue}&destType=${this.destTypevalue}&`
        )
        .then(res => {
          console.log(res.data);
          this.$message(res.data);
        });
    
    },
  }
};
</script>

<style>
.xiaxing {
  width: 500px;
  height: 300px;
  position: absolute;
  top: 200px;
  right: 0px;
}
</style>


