/****   request.js   ****/
// axios
import axios from 'axios'
import QS from 'qs';
import router from '../router'
// element-ui Message
import { Message } from 'element-ui';
//1. axios
const service = axios.create({
        // 
        baseURL: process.env.VUE_APP_URL,
        //  ms60s
        timeout: 60 * 1000
    })
    // 2.
service.interceptors.request.use(config => {
    //token,loading
    // const token = getCookie('');cookiejs-cookie
    config.headers = {
        'Content-Type': 'application/x-www-form-urlencoded'
    }
    if (config.method === 'post') {
        config.data = QS.stringify({
            ...config.data //   a=xx&b=xx&c=xx
        });
    }
    // if(token){
    //   config.params = {'token':token}
    // }
    return config
}, error => {
    Promise.reject(error)
})

// 3.
service.interceptors.response.use(response => {
        //loading

        return response
    }, error => {
        /*****  *****/
        if (error && error.response) {
            // 1.
            // 2.
            switch (error.response.status) {
                case 400:
                    error.message = ''
                    break;
                case 401:
                    error.message = ''
                    router.replace({ path: '/signin', replace: true })
                    break;
                case 403:
                    error.message = ''
                    break;
                case 404:
                    error.message = ','
                        // window.location.href = "/NotFound"
                    break;
                case 405:
                    error.message = ''
                    break;
                case 408:
                    error.message = ''
                    break;
                case 500:
                    error.message = ''
                    break;
                case 501:
                    error.message = ''
                    break;
                case 502:
                    error.message = ''
                    break;
                case 503:
                    error.message = ''
                    break;
                case 504:
                    error.message = ''
                    break;
                case 505:
                    error.message = 'http'
                    break;
                default:
                    error.message = `${error.response.status}`
            }
        } else {
            // 
            if (JSON.stringify(error).includes('timeout')) {
                Message.error('')
            }
            error.message('')
        }

        Message.error(error.message)
            /*****  *****/
            //
        return Promise.resolve(error.response)
    })
    //4.
export default service


